import fanYong from "./component/fanYong";
import invite from "./component/invite";
export default {
  data() {
    return {
      tabIndex: 0,
      active: Number,
      currentView: String,
      tabs: [
        {
          type: "返佣记录",
          view: "fanYong"
        },
        {
          type: "邀请进度",
          view: "invite"
        }
      ]
    };
  },
  components: {
    fanYong,invite
  },
  mounted() {
    if (this.$route.params.index == "one") {
      this.active = 1;
      this.currentView = "invite";
    } else {
      this.active = 0;
      this.currentView = "fanYong";
    }
  },
  methods: {
    toggle(i, v) {
      this.active = i;
      this.currentView = v;
    }
  }
};



